      obscure: "0:3:nbhSyeIZzpEpBHF1yw6Xgf/fs2KukS/OnVCnAy5Gnkyu5bU=",
      password: "plainpassword",
      colorpicker: "#ABBB33",
      datetimepicker: "2024-12-27 02:01:20",
